import { ErrorHandler, NgModule } from '@angular/core';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';
import { BrowserModule } from '@angular/platform-browser';
import { HttpClientModule } from '@angular/common/http';
import { jobGreen } from './app.component';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Keyboard } from '@ionic-native/keyboard';
import { PruebaServiceProvider } from '../providers/prueba-service/prueba-service';
import { Camera } from '@ionic-native/camera';
import { DenunciaCrearPage } from '../pages/inicio/denuncia/denuncia-crear/denuncia-crear';

@NgModule({
  declarations: [
    jobGreen
  ],
  imports: [
    BrowserModule,
		HttpClientModule,
		IonicModule.forRoot(jobGreen, {
			preloadModules: true,
			scrollPadding: false,
			scrollAssist: true,
			autoFocusAssist: false
    }, 
{
  links: [
    { loadChildren: '../pages/ayuda/about/about.module.ngfactory#AboutPageModuleNgFactory', name: 'page-about', segment: 'about', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/ayuda/soporte/soporte.module.ngfactory#SoportePageModuleNgFactory', name: 'page-soporte', segment: 'soporte', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/configuracion/cnfg-puntosrecoleccion/cnfg-puntosrecoleccion.module.ngfactory#CnfgPuntosrecoleccionPageModuleNgFactory', name: 'page-cnfg-puntosrecoleccion', segment: 'cnfg-puntosrecoleccion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/configuracion/cnfg-puntosrecoleccion/pun-rec-editar/pun-rec-editar.module.ngfactory#PunRecEditarPageModuleNgFactory', name: 'PunRecEditarPage', segment: 'pun-rec-editar', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/denuncia/denuncia.module.ngfactory#DenunciaPageModuleNgFactory', name: 'DenunciaPage', segment: 'denuncia', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/denuncia/denuncia-crear/denuncia-crear.module.ngfactory#DenunciaCrearPageModuleNgFactory', name: 'DenunciaCrearPage', segment: 'denuncia-crear', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/inicio.module.ngfactory#InicioPageModuleNgFactory', name: 'page-inicio', segment: 'inicio', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/puntos-recoleccion/puntos-recoleccion.module.ngfactory#PuntosRecoleccionPageModuleNgFactory', name: 'PuntosRecoleccionPage', segment: 'puntos-recoleccion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/solicitud-recoleccion/sol-recoleccion-crear/sol-recoleccion-crear.module.ngfactory#SolRecoleccionCrearPageModuleNgFactory', name: 'SolRecoleccionCrearPage', segment: 'sol-recoleccion-crear', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/solicitud-recoleccion/solicitud-recoleccion.module.ngfactory#SolicitudRecoleccionPageModuleNgFactory', name: 'SolicitudRecoleccionPage', segment: 'solicitud-recoleccion', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/inicio/regalos/regalos.module.ngfactory#RegalosPageModuleNgFactory', name: 'RegalosPage', segment: 'regalos', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/login/login.module.ngfactory#AuthPageModuleNgFactory', name: 'page-login', segment: 'login', priority: 'high', defaultHistory: [] },
    { loadChildren: '../pages/perfil/perfil.module.ngfactory#PerfilPageModuleNgFactory', name: 'page-perfil', segment: 'perfil', priority: 'low', defaultHistory: [] }
  ]
}),
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    jobGreen
  ],
  providers: [
    StatusBar,
    SplashScreen,
    Keyboard,
    Camera,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    PruebaServiceProvider
  ]
})
export class AppModule {}



